<template>
	<v-card
		class="mx-4 my-8 kart"
		width="344"
		elevation="12"
		dark
		:class="isSubmited&&!error?'kart-submited':error&&'kart-error'"
	>
		<v-card-text>
			<p class="headline">
				{{title}}
			</p>
		</v-card-text>
		<v-card-actions>
			<v-btn-toggle
				v-model="opinion"
				active-class="active"
				mandatory
				borderless
				tile
				class="btn-toggler"
				background-color="transparent"
				v-on:change="submitResult"
			>
				<v-btn
					depressed
					v-for="q in agressionQuizOpinionTypes"
					:value="q"
					:key="q.id"
					:style="{
						backgroundColor: opinion.id ===q.id ?'#4CAF50': '#fff',
						color: opinion.id ===q.id ?'#fff':'#000'
					}"
					x-small
				>
					{{q.label}}
				</v-btn>
			</v-btn-toggle>
			<v-btn
				absolute
				dark
				replace
				:style="{
					top: '-20px',
					right: '-20px',
					backgroundColor: isSubmited?'#555!important':'#4caf50!important',
					color: '#ddd!important'
				}"
			>
				{{opinion.label||'---'}}
			</v-btn>
		</v-card-actions>
	</v-card>
</template>

<script>
	import {agressionQuizOpinionTypes} from '@/components/Statics';

	export default {
		name: "QuestionType1",
		props: {
			id: {
				type: Number
			},
			title: {
				type: String
			},
			err: {
				type: Boolean,
				default() {
					return false
				}
			}
		},
		computed: {},
		data() {
			return ({
				agressionQuizOpinionTypes,
				opinion: 0,
				isSubmited: false,
				error: this.$props.err
			})
		},
		watch: {
			err: function (newVal, oldVal) { // watch it
				if (newVal !== oldVal)
					this.error = newVal
			}
		},
		methods: {
			submitResult(event) {
				if (!this.isSubmited) {
					this.$emit("handleResult", this.$props.id, this.opinion);
					this.isSubmited = true;
					this.error = false;
				}
			}
		}
	}
</script>

<style scoped>
	.active {
		background-color: #4caf50;
		color: white;
	}

	.btn-toggler {
		color: aliceblue;
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		display: flex;
		justify-content: space-between;
	}

	.kart-submited {
		/*background-color: #4caf50;*/
		color: rgba(255, 255, 255, 1);
	}

	.kart-error {
		background-color: rgba(216, 0, 12, 0.82);
		color: rgba(255, 255, 255, 1);
		animation: shake 0.82s cubic-bezier(.36, .07, .19, .97) both;
		transform: translate3d(0, 0, 0);
		backface-visibility: hidden;
		perspective: 1000px;
	}

	.kart-error:hover {
		animation: shake 0.82s cubic-bezier(.36, .07, .19, .97) both;
		transform: translate3d(0, 0, 0);
		backface-visibility: hidden;
		perspective: 1000px;
	}

	@keyframes shake {
		10%, 90% {
			transform: translate3d(-1px, 0, 0) rotate3d(1, 1, 1, -1deg);
		}

		20%, 80% {
			transform: translate3d(2px, 0, 0) rotate3d(1, 1, 1, 2deg);
		}

		30%, 50%, 70% {
			transform: translate3d(-4px, 0, 0) rotate3d(1, 1, 1, -4deg);
		}

		40%, 60% {
			transform: translate3d(4px, 0, 0) rotate3d(1, 1, 1, 4deg);
		}
	}

	.kart {
		width: 30%;
		min-height: 200px;
	}
</style>
