<template>
	<v-container>
		<v-layout>
			<v-sheet elevation="1" max-width="100%">
				<v-col cols="12" class="pa-0">
					<v-banner single-line class="banner pa-5" min-width="100%" elevation="10" dark>
						<p class="banner-text display-1 font-weight-bold">DƏYƏRLƏRİN İCMALI</p>
					</v-banner>
					<v-banner single-line class="banner pa-5" min-width="100%" elevation="10"
							  v-if="false">
						<v-btn color="primary" @click="dummyData">Dummy data</v-btn>
					</v-banner>
					<v-sheet style="background-color: #E8470C" elevation="8" class="pa-6 text-justify" dark>
						<p class="headline">
							TƏLİMAT: Bu sorğu vərəqində siz bir suala cavab verməlisiniz: “Mənim həyatımın aparıcı
							prinsipləri
							kimi hansı dəyərlər vacibdir və mənim üçün hansı dəyərlər daha az vacibdir?”. Sizin
							vəzifəniz
							həyatınızın aparıcı prinsipi kimi hər dəyərin vaciblik dərəcəsini qiymətləndirməkdən
							ibarətdir.
							-1-dən 7-yə qədər dərəcəli qiymətləndirmə şkalasını istifadə edin. Nömrə nə qədər yüksək
							olarsa
							(-1,
							0, 1, 2, 3, 4, 5, 6, 7), bir o qədər bu dəyər sizin həyatınızın aparıcı prinsipi kimi
							daha vacibdir. Aşağıdakı dəyərlər siyahısından maksimum 2-sini ən əhəmiyyətli olaraq
							dəyərləndirib 7 balla qiymətləndirin.
						</p>
						<v-divider class="mt-2 mb-4"/>
						<p class="headline">Mənim həyatımın aparıcı prinsipi kimi bu dəyər:</p>
						<v-simple-table dense>
							<template v-slot:default>
								<thead>
								<tr>
									<th class="text-center">Mənim prinsiplərimə ziddir</th>
									<th class="text-center">Vacib deyil</th>
									<th class="text-center ">Vacibdir</th>
									<th class="text-center">Çox vacibdir</th>
									<th class="text-center">Daha çox əhəmiyyətlidir</th>
								</tr>
								</thead>
								<tbody>
								<tr>
									<td class="text-center">-1</td>
									<td class="text-center">0</td>
									<td class="text-center">
										<ul class="list">
											<li>1</li>
											<li>2</li>
											<li>3</li>
											<li>4</li>
											<li>5</li>
										</ul>
									</td>
									<td class="text-center">6</td>
									<td class="text-center">7</td>
								</tr>
								</tbody>
							</template>
						</v-simple-table>
					</v-sheet>
					<v-divider class="my-1"/>
					<v-sheet dark elevation="12"
							 class="question-sheet d-flex flex-row flex-wrap justify-space-around align-center">
						<QuestionType1
							v-for="(q,i) in schwartzQuestions1"
							:key="q.id"
							:id="q.id"
							@handleResult="handleResult"
							:label="q.label"
							:description="q.description"
							:err="errors[i]"
						/>
					</v-sheet>
					<v-row justify="center" align-content="center" no-gutters>
						<v-btn elevation="12"
							   width="50%"
							   min-height="60"
							   class="my-5 align-self-start"
							   type="submit"
							   color="primary"
							   @click="handleSubmit"
							   large
							   outlined>
							Növbəti
							<v-icon>mdi-chevron-right</v-icon>
						</v-btn>
					</v-row>
				</v-col>
			</v-sheet>
		</v-layout>
	</v-container>
</template>

<script>
	import {
		user_does_not_exist_title,
		user_does_not_exist_message,
		form_not_valid_message,
		quiz_completed_message
	} from "./messages";
	import {dataStore} from "../firebase";
	import QuestionType1 from "./QuestionType1";
	import {schwartzQuiz1} from "./Statics";
	import {mapState} from "vuex";

	export default {
		name: "Schwartz1",
		components: {
			QuestionType1
		},
		mounted() {
			if (this.user === null) {
				alert('No user');
				this.$router.replace('/')
			}
			this.$store.dispatch('setActiveStepper', this.$route.meta.index);
		},
		created() {
			this.toast = this.$swal.mixin({
				toast: true,
				position: 'top-end',
				type: 'error',
				showConfirmButton: false,
				timer: 3000,
				showCloseButton: true,
				padding: '10px',
				onOpen: (toast) => {
					toast.addEventListener('mouseenter', this.$swal.stopTimer);
					toast.addEventListener('mouseleave', this.$swal.resumeTimer)
				}
			});
		},
		computed: {
			...mapState(["user"]),
			activeStepper() {
				return this.$store.state.activeStepper
			}
		},
		data: () => ({
			schwartzQuestions1: schwartzQuiz1,
			errors: [],
		}),
		methods: {
			checkSevens() {
				let count = 0;
				this.schwartzQuestions1.forEach(q => {
					if (q.opinion === 7) {
						count++;
					}
				});
				this.sevenError = count <= 2;
				return this.sevenError
			},
			isValid() {
				let valid = true;
				this.errors = this.schwartzQuestions1.map(q => {
					console.log("q -> ", q);
					if (q.opinion !== 0 && !q.opinion) {
						valid = false;
						return true;
					} else return false
				});
//				if (!this.checkSevens())
//					valid = false;
				return valid;
			},
			handleResult(id, opinion) {
				this.schwartzQuestions1 = this.schwartzQuestions1.map(q => {
					if (q.id === id) {
						q.opinion = opinion;
					}
					return q;
				});
			},
			handleSubmit() {
				if (this.isValid()) {
					console.log("valid", this.schwartzQuestions1);
					const user = this.$store.state.user;
					if (user && user.personalData) {
						user.schwartzResults = {part1: this.schwartzQuestions1};
						user.quiz1 = true;
						dataStore
							.collection("results")
							.doc(user.personalData.id)
							.set(user)
							.then(() => {
								this.$store.dispatch('setUser', user);
								this.toast.fire({
									type: 'success',
									title: quiz_completed_message
								});
								this.$router.push({name: "schwartz2"});
							});
					} else {
						this.$swal({
							title: user_does_not_exist_title,
							text: user_does_not_exist_message,
							type: "error",
							showCancelButton: true,
							showConfirmButton: false,
							cancelButtonText: "OK"
						});
					}
				} else {
					this.toast.fire({
						type: 'error',
						title: form_not_valid_message
					})
				}
			},
			dummyData() {
				this.schwartzQuestions1.forEach((que, i) => {
					this.handleResult(i, 0)
				})
				console.log("schwartzQuestions1 -> ", this.schwartzQuestions1);
			}
		},
	}
</script>

<style scoped>
	.question-sheet {
		height: 800px;
		padding: 2rem;
		overflow: scroll;
	}

	.banner {
		color: #ffffff;
		display: flex;
		text-align: center;

	}

	.banner-text {
		margin: 0;
	}

	.list {
		text-decoration: none;
		list-style: none;
		display: flex;
		justify-content: space-evenly;
		padding: 0;
	}

	.list li {
		/*border: 1px solid #fff;*/
	}

	.v-banner__text {
		width: 100%;
	}

	.paragraph-fill {
		width: 100%;
	}
</style>
