<template>
	<v-container>
		<v-layout>
			<v-sheet elevation="1" max-width="100%">
				<v-col cols="12" class="pa-0">
					<v-banner class="banner" min-width="100%" elevation="10" dark>
						<p class="banner-text display-1 font-weight-bold mb-0 paragraph-fill font-weight-bold text-center">
							L.İ.VASSERMANIN SOSİAL FRUSTRASİYA SƏVİYYƏSİNİN DİAQNOSTİK METODİKASI
						</p>
						<p class="text--white headline font-weight-bold mb-0 paragraph-fill font-weight-bold text-center">
							V.V.Boykonun modifikasiyası
						</p>

					</v-banner>
					<v-banner style="background-color: #E8470C" class="banner d-flex justify-center" dark
							  min-width="100%" elevation="1">
						<template>
							<p class="mb-0 paragraph-fill headline font-weight-bold text-left">
								TƏLİMAT: Aşağıda verilmiş sorğuda həyat fəaliyyətinin əsas aspektlərindəki sosial
								nailiyyətlərdən məmnuniyyətsizliyin dərəcəsi yoxlanılır. Hər bir cavabı oxuyun və ən
								uyğun gələn cavabı qeyd edin.
							</p>
						</template>
					</v-banner>
					<v-divider class="my-6"/>
					<v-banner sticky dark class="banner d-flex justify-center"
							  min-width="100%" elevation="12">
						<template>
							<p class="mb-0 paragraph-fill headline font-weight-bold text-left">
								Siz aşağıda adı çəkilən hallardan (hadisələrdən) məmnunsunuzmu?
							</p>
						</template>
					</v-banner>
					<v-divider class="my-1"/>
					<v-sheet dark elevatio="12"
							 class="question-sheet">
						<template v-slot:default>
							<v-sheet class="table-header">
								<v-row class="table-row">
									<v-col cols="5"/>
									<v-col cols="7">
										<v-row no-gutters>
											<v-col style="width: 20%" class="text-center"
												   v-for="k in vassermanQuizOpinionTypes">
												{{k.title}}
											</v-col>
										</v-row>
									</v-col>
								</v-row>
							</v-sheet>
							<v-sheet elevation="0" class="table-body">
								<QuestionType2
									:title="q.title"
									v-for="(q,i) in vassermanQuiz"
									:id="q.id"
									:key="q.id"
									:error="errors[i]"
									:types="vassermanQuizOpinionTypes"
									:test="test"
									@handleError="handleError"
									@handleResult="handleResult"/>
							</v-sheet>
						</template>
					</v-sheet>
					<v-row justify="center" align-content="center" no-gutters class="px-12">
						<v-btn elevation="12"
							   width="50%"
							   min-height="60"
							   class="my-5 align-self-start"
							   type="submit"
							   color="primary"
							   @click="handleSubmit"
							   large
							   outlined>
							Növbəti
							<v-icon>mdi-chevron-right</v-icon>
						</v-btn>
					</v-row>
				</v-col>
			</v-sheet>
		</v-layout>
	</v-container>
</template>

<script>
	import {
		form_not_valid_message,
		user_does_not_exist_message,
		user_does_not_exist_title,
		quiz_completed_message
	} from "./messages";
	import {dataStore} from "../firebase";
	import {vassermanQuiz, vassermanQuizOpinionTypes} from "./Statics"
	import QuestionType2 from "./QuestionType2";
	import {mapState, mapActions} from "vuex";

	export default {
		name: "Vasserman",
		mounted() {
			if (this.user === null) {
				alert('No user');
				this.$router.replace('/')
			}
			this.$store.dispatch("setActiveStepper", this.$route.meta.index);
		},
		computed: {
			...mapState(["user"])
		},
		components: {QuestionType2},
		created() {
			this.errors = this.vassermanQuiz.filter(() => false);
			this.toast = this.$swal.mixin({
				toast: true,
				position: 'top-end',
				type: 'error',
				showConfirmButton: false,
				timer: 3000,
				showCloseButton: true,
				padding: '10px',
				onOpen: (toast) => {
					toast.addEventListener('mouseenter', this.$swal.stopTimer);
					toast.addEventListener('mouseleave', this.$swal.resumeTimer)
				}
			});
		},
		data: () => ({
			opinion: true,
			vassermanQuiz,
			errors: [],
			vassermanQuizOpinionTypes,
			test: "beledi"
		}),
		methods: {
			handleError(id, status) {
				const errs = this.errors;
				errs[id] = status;
				this.errors = errs;
				this.test = Math.random(0, 1).toString();
			},
			handleResult(id, opinion) {
				this.vassermanQuiz.filter((q, i) => {
					if (q.id === id) {
						this.vassermanQuiz[i].opinion = this.vassermanQuizOpinionTypes[opinion];
					}
				});

			},
			handleSubmit() {
				this.test = "deyisdi";
				const valid = this.isValid();
				if (valid) {
					if (this.user.personalData && this.user.personalData.id) {
						this.user.vassermanQuizResults = vassermanQuiz;
						this.user.quiz4 = true;
						dataStore
							.collection("results")
							.doc(this.user.personalData.id)
							.set(this.user)
							.then(() => {
								this.$store.dispatch('setUser', this.user);
								this.toast.fire({
									type: 'success',
									title: quiz_completed_message
								});
								this.$router.push({name: "agression"});
							});
					} else {
						this.$swal({
							title: user_does_not_exist_title,
							text: user_does_not_exist_message,
							type: "error",
							showCancelButton: true,
							showConfirmButton: false,
							cancelButtonText: "OK"
						});
					}
				} else {
					this.toast.fire({
						type: 'error',
						title: form_not_valid_message
					})

				}
			},
			isValid() {
				let valid = true;
				this.vassermanQuiz.filter((q, i) => {
					if (!q.opinion) {
						this.errors[i] = true;
						valid = false;
					}
				});
				return valid
			}
		}
	}
</script>

<style scoped lang="scss">

	.v-input__control {
		width: 100% !important
	}

	.table-header {
		.table-row {
			width: 100%;

			.table-col {
				width: 20%;
			}
		}

	}

	.table-body {
		width: 100%;
	}

</style>
