<template>
	<v-card
		class="mx-4 my-8 kart"
		shaped
		max-width="444"
		elevation="12"
		:light="!isSubmited"
		:class="isSubmited&&!error?'kart-submited':error&&'kart-error'"
	>
		<v-card-text>
			<p class="headline" :style="{color: isSubmited?'#fff':'#000'}">
				{{label}}
			</p>
			<!--			<p>adjective</p>-->
			<div class="" :style="{color: isSubmited?'#fff':'#000'}">
				{{description}}
			</div>
		</v-card-text>
		<v-card-actions>
			<v-item-group class="flex flex-fill"
						  v-model="selectedItem"
						  @change="handleChange"
						  mandatory>
				<v-row dense>
					<v-col
						v-for="n in evalutionList"
						:key="n"
						cols="24"
						style="width: 7.5%"
					>
						<v-item v-slot:default="{ active, toggle }">
							<v-card
								:disabled="isSubmited"
								:color="active ? 'primary' : ''"
								class="d-flex align-center"
								dark
								height="40"
								@click="toggle"
							>
								<v-scroll-y-transition>
									<div

										class="flex-grow-1 text-center"
									>
										{{n}}
									</div>
								</v-scroll-y-transition>
							</v-card>
						</v-item>
					</v-col>
				</v-row>
			</v-item-group>
			<v-tooltip bottom>
				<template v-slot:activator="{ on }">
					<v-btn
						v-on="on"
						absolute
						dark
						fab
						@click="submitResult"
						right
						bottom
						class="submit-fab"
						color="primary"
					>
						<v-icon>{{isSubmited ? "mdi-pencil" : "mdi-check"}}</v-icon>
					</v-btn>
				</template>
				<span>{{isSubmited?"redaktə et":"təsdiq et"}}</span>
			</v-tooltip>
			<!--			<v-btn-->
			<!--				absolute-->
			<!--				dark-->
			<!--				fab-->
			<!--				@click="submitResult"-->
			<!--				bottom-->
			<!--				class="submit-fab"-->
			<!--				right-->
			<!--				color="primary"-->
			<!--			>-->
			<!--				<v-tooltip :value="isSubmited?'redaktə et':'təsdiq et'" bottom dark activator=".submit-fab" />-->
			<!--					<v-icon>{{isSubmited ? "mdi-pencil" : "mdi-check"}}</v-icon>-->
			<!--			</v-btn>-->
			<v-btn
				absolute
				dark
				fab
				small
				replace
				top
				right
				:disabled="isSubmited"
				:style="{
					backgroundColor: isSubmited||error?'#555!important':'#4caf50!important',
					color: '#ddd!important'
				}"
			>
				{{result}}
			</v-btn>
		</v-card-actions>
	</v-card>
</template>

<script>
	export default {
		name: "QuestionType1",
		props: {
			id: {
				type: Number
			},
			label: {
				type: String
			},
			description: {
				type: String
			},
			err: {
				type: Boolean,
				default() {
					return false
				}
			}
		},
		computed: {},
		data: function () {
			return ({
				selectedItem: null,
				result: null,
				evalutionList: [-1, 0, 1, 2, 3, 4, 5, 6, 7],
				isSubmited: false,
				error: this.$props.err
			})
		},
		watch: {
			err: function (newVal, oldVal) { // watch it
				if (newVal !== oldVal)
					this.error = newVal
			}
		},
		methods: {
			handleChange() {
				this.result = this.evalutionList[this.selectedItem];
			},
			submitResult() {
				if (!this.isSubmited) {
					this.$emit("handleResult", this.$props.id, this.result);
					this.isSubmited = true;
					this.error = false;
				} else
					this.isSubmited = false;
			}
		}
	}
</script>

<style scoped>
	.kart-submited {
		background-color: #4caf50;
		color: rgba(255, 255, 255, 1);
	}

	.kart-error {
		background-color: rgba(216, 0, 12, 0.82);
		color: rgba(255, 255, 255, 1);
		animation: shake 0.82s cubic-bezier(.36, .07, .19, .97) both;
		transform: translate3d(0, 0, 0);
		backface-visibility: hidden;
		perspective: 1000px;
	}

	.kart-error:hover {
		animation: shake 0.82s cubic-bezier(.36, .07, .19, .97) both;
		transform: translate3d(0, 0, 0);
		backface-visibility: hidden;
		perspective: 1000px;
	}

	@keyframes shake {
		10%, 90% {
			transform: translate3d(-1px, 0, 0) rotate3d(1, 1, 1, -1deg);
		}

		20%, 80% {
			transform: translate3d(2px, 0, 0) rotate3d(1, 1, 1, 2deg);
		}

		30%, 50%, 70% {
			transform: translate3d(-4px, 0, 0) rotate3d(1, 1, 1, -4deg);
		}

		40%, 60% {
			transform: translate3d(4px, 0, 0) rotate3d(1, 1, 1, 4deg);
		}
	}

	.kart {
		width: 40%;
		height: 200px;
	}
</style>
