import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
	state: {
		userId: "",
		user: {},
		activeStepper: 0
	},
	mutations: {
		setUserId: (state, id) => {
			state.userId = id
		},
		setUser: (state, user) => {
			state.user = user
		},
		setActiveStepper: (state, index) => {
			state.activeStepper = index
		},
	},
	actions: {
		setUserId({commit, state}, id) {
			commit("setUserId", id);
		},
		setUser({commit, state}, user) {
			commit("setUser", user);
		},
		setActiveStepper({commit, state}, index) {
			commit("setActiveStepper", index);
		}

	},
	getters: {
		getUserId: state => state.userId,
		getUser: state => state.user,
		getActiveStepper: state => state.activeStepper
	},
});

export default store;
