<template>
	<v-container fluid>
		<v-layout>
			<v-sheet elevation="1" max-width="100%">
				<v-col cols="12" class="pa-0">
					<v-banner class="banner pa-5" min-width="100%" elevation="10" dark>
						<p class="banner-text display-1 font-weight-bold">
							ƏSAS TƏLƏBATLARIN ÖDƏNILMƏSI DƏRƏCƏSININ DIAQNOSTIK METODIKASI
						</p>
					</v-banner>
					<v-sheet style="background-color: #E8470C" elevation="8" class="pa-6 text-justify" dark>
						<p class="headline">
							TƏLİMAT: Sizin qarşınızda 15 müddəa var, siz onları cüt-cüt götürərək
							öz aralarında müqayisə etməli və qiymətləndirməlisiniz.
							Deyək ki, başlayırıq 1-ci müddəa ilə. Birinci müddəanı özündən aşağıdakılar ilə yəni
							2-ci, 3-cü ... 15-ci ilə müqayisə edəcəyik. İlk öncə 1-cini 2-ci ilə müqayisə edirik.
							Əgər 1-cinə üstünlük veririksə nəticəni 1-ci sütunun altındakı 2 yazılan xanaya 1 olaraq
							qeyd edirik. Yox əgər 2-cinə üstünlük veririksə nəticəni həmin xanaya 2 olaraq qeyd
							edirik. Bundan sonra 2-ci müddəanı eyni qayda ilə işləyirik: əvvəl onu 3-cü ilə müqayisə
							edirik, daha sonra 4-cü ilə və s. və hansnı üstün sayırıqsa nömrəsini altındakı
							müqayisə etdiyiniz müddəanın nömrəsi olan xanaya qeyd edirik.
							Və beləcə bütün blankı dolduruq.
						</p>

						<v-divider class="mt-2 mb-4"/>
						<div>
							<v-banner dark tile>
								Hər bir müddəa üçün iş zamanı “Mən istəyirəm...” frazasını səsləndirmək məsləhətdir.
							</v-banner>
							<v-row dense>
								<v-col>
									<v-banner dark>
										<p v-for="item in needsList" class="mb-1">
											{{item.id+1+'. '+item.label}}
										</p>
									</v-banner>
								</v-col>
							</v-row>
						</div>

					</v-sheet>
					<v-divider class="my-4"/>
					<v-sheet dark
							 elevation="12"
							 width="100%"
							 class="question-sheet d-flex flex-column justify-start align-start">
						<v-form v-model="valid" id="needs-form" @submit.prevent="handleSubmit" ref="dataForm"
								class="question-sheet d-flex flex-column justify-start align-start">
							<v-row v-for="t in numList" :key="t" dense :class="t===14?'':'ml-1'">
								<v-col v-for="k in t+1" :key="k">
									<div class="d-flex flex-row" v-if="t===14">
										<v-card light class="tile" :class="k===1?'ml-2':''">
											{{k}}
										</v-card>
									</div>
									<div v-else class="d-flex flex-row">
										<v-card light class="tile">
											<v-text-field
												dense
												type="number"
												hide-details
												:disabled="loading"
												:placeholder="k+14-t+''"
												class="ma-0 pa-0 d-flex justify-center text-center input"
												:rules="[val => val==k||val==k+14-t||'']"
												v-on:change="handleChange"
												v-model="results[k-1][13-t]"/>
										</v-card>
									</div>
								</v-col>
							</v-row>
						</v-form>


					</v-sheet>
					<v-row justify="center" align-content="center" no-gutters>
						<v-btn elevation="12"
							   width="50%"
							   min-height="60"
							   :disabled="loading"
							   class="my-5 align-self-start"
							   type="submit"
							   color="primary"
							   @click="handleSubmit"
							   large
							   outlined>
							Növbəti
							<v-icon>mdi-chevron-right</v-icon>
						</v-btn>
					</v-row>
				</v-col>
			</v-sheet>
		</v-layout>
	</v-container>
</template>

<script>
	import {
		user_does_not_exist_message,
		quiz_completed_message,
		user_does_not_exist_title,
		form_not_valid_message
	} from "./messages";
	import {dataStore} from "../firebase";
	import {needsList} from "./Statics";
	import {mapState} from "vuex";

	export default {
		name: "Needs",
		created() {
			this.toast = this.$swal.mixin({
				toast: true,
				position: 'top-end',
				type: 'error',
				showConfirmButton: false,
				timer: 3000,
				showCloseButton: true,
				padding: '10px',
				onOpen: toast => {
					toast.addEventListener('mouseenter', this.$swal.stopTimer);
					toast.addEventListener('mouseleave', this.$swal.resumeTimer);
				}
			});
		},
		mounted() {
			if (this.user === null) {
				alert('No user');
				this.$router.replace('/')
			}
			this.$store.dispatch("setActiveStepper", this.$route.meta.index);
		},
		data() {
			return {
				results: [
					[
						null, null, null, null, null, null, null, null, null, null, null, null, null, null,
					], [
						null, null, null, null, null, null, null, null, null, null, null, null, null,
					], [
						null, null, null, null, null, null, null, null, null, null, null, null,
					], [
						null, null, null, null, null, null, null, null, null, null, null,
					], [
						null, null, null, null, null, null, null, null, null, null,
					], [
						null, null, null, null, null, null, null, null, null,
					], [
						null, null, null, null, null, null, null, null,
					], [
						null, null, null, null, null, null, null,
					], [
						null, null, null, null, null, null,
					], [
						null, null, null, null, null,
					], [
						null, null, null, null,
					], [
						null, null, null,
					], [
						null, null,
					], [
						null,
					],
				],
				needsList,
				errors: [],
				valid: true,
				loading: false
			}
		},
		methods: {
			handleChange() {
			},
			validate() {
				let isValid = true;
				this.results.forEach(row => {
					row.forEach(item => {
						if (!item)
							isValid = false
					})
				});
				return isValid;
			},
			prepareResults: res => res.map((k, i) => ({
				id: i,
				comparisonNo: i + 1,
				comparisonResult: k.map((item, k) => ({result: Number(item), compareNum: i + k + 2}))
			})),
			async handleSubmit() {
				const valid = this.$refs.dataForm.validate();
				this.loading = true;
				console.log("valid -> ", valid);
				if (this.validate() && valid) {
					const user = this.$store.state.user;
					if (user && user.personaldata) {
						user.needsQuizResults = this.prepareResults(this.results);
						user.quiz3 = true;
						dataStore
							.collection("results")
							.doc(user.personalData.id)
							.set(user)
							.then(() => {
								this.$store.dispatch('setUser', user);
								this.toast.fire({
									type: 'success',
									title: quiz_completed_message
								});
								this.$router.push({name: "vasserman"});
							}).catch().finally(() => this.loading = false);
					} else {
						this.$swal({
							title: user_does_not_exist_title,
							text: user_does_not_exist_message,
							type: "error",
							showCancelButton: true,
							showConfirmButton: false,
							cancelButtonText: "OK"
						});
						this.loading = false;
					}
				} else {
					this.loading = false;
					await this.toast.fire({
						type: 'error',
						title: form_not_valid_message
					})
				}
			}
		},
		computed: {
			...mapState(["user"]),
			numList() {
				return [...Array(needsList.length).keys()].slice().reverse()
			}
		},
	}
</script>

<style lang="scss">
	.tile {
		width: 50px;
		height: 50px;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 2rem;

		input {
			text-align: center
		}
	}

	input::-webkit-outer-spin-button,
	input::-webkit-inner-spin-button {
		-webkit-appearance: none;
		margin: 0;
	}

	.question-sheet {
		/*height: 800px;*/
		padding: 2rem;
		/*overflow: scroll;*/
	}

	.banner {
		color: #ffffff;
		display: flex;
		text-align: center;

	}

	.banner-text {
		margin: 0;
	}

	.list {
		text-decoration: none;
		list-style: none;
		display: flex;
		justify-content: space-evenly;
		padding: 0;
	}

	.list li {
		/*border: 1px solid #fff;*/
	}

	.v-banner__text {
		width: 100%;
	}

	.paragraph-fill {
		width: 100%;
	}
</style>
