									</p>
									<p class="text--secondary text-center">
										Daxil etdiyiniz məlumatlar dissertasiya işində statistik olaraq istifadə
										olunacaq və
										anonimlik tam olaraq qorunacaq🙂
									</p>
								</v-row>
								<v-row>
									<v-col md="6" xs="12" justify-self="center" class="pa-10">
										<v-row
											cols="12"
											md="4"
										>
											<v-text-field
												:disabled="loading"
												v-model="firstname"
												:rules="[requiredRule]"
												:counter="10"
												label="Ad"
												required
											/>
										</v-row>
										<v-row
											cols="12"
											md="4"
										>
											<v-text-field
												:disabled="loading"
												v-model="lastname"
												:rules="[requiredRule]"
												:counter="10"
												label="Soyad"
												required
											/>
										</v-row>
										<v-row
											cols="12"
											md="4"
										>
											<v-text-field
												:disabled="loading"
												number
												v-model="age"
												type="number"
												:rules="[requiredRule, ageRule]"
												label="Yaş"
												required
											/>

										</v-row>
										<v-row
											cols="12"
											md="4"
										>
											<v-text-field
												:disabled="loading"
												v-model="email"
												type="email"
												label="Email"
												required
												hint="Nəticələri emailinizə göndərməyimizi istəyirsinizsə daxil edin"
											/>

										</v-row>
										<v-row
											cols="12"
											md="4">
											<v-select
												:disabled="loading"
												required
												:rules="[requiredRule]"
												item-text="label"
												:items="genders"
												label="Cins"
												v-model="gender"
											/>
										</v-row>
										<v-row
											cols="12"
											md="6">
											<v-select
												:disabled="loading"
												required
												:rules="[requiredRule]"
												item-text="label"
												:items="social_status_list"
												label="Sosial status"
												v-model="social_status"
											/>

										</v-row>
										<v-row
											cols="12"
											md="6">
											<v-select
												:disabled="loading"
												required
												:rules="[requiredRule]"
												item-text="label"
												:items="education_list"
												label="Təhsil"
												v-model="education"
											/>

										</v-row>
									</v-col>
									<v-col md="6" xs="12" justify-self="center" class="pa-10">
										<v-row
											cols="12"
											md="6">
											<v-select
												:disabled="loading"
												required
												:rules="[requiredRule]"
												item-text="label"
												:items="martial_status_list"
												label="Ailə vəziyyəti"
												v-model="martial_status"
											/>
										</v-row>
										<v-row
											cols="12"
											md="6">
											<v-select
												:disabled="loading"
												required
												:rules="[requiredRule]"
												item-text="label"
												:items="residance_place_list"
												label="Yaşayış yeri"
												v-model="residance_place"
											/>
										</v-row>
										<v-row
											cols="12"
											md="4"
										>
											<v-text-field
												:disabled="loading"
												number
