												type="number"
												:rules="[requiredRule, numberRule]"
												label="Ailə üzvlərinin sayı"
												required
											/>
										</v-row>
										<v-row
											cols="12"
											md="4">
											<v-select
												:disabled="loading"
												required
												:rules="[requiredRule]"
												item-text="label"
												:items="residance_condition_list"
												label="Yaşayış şəraiti"
												v-model="residance_condition"
											/>
										</v-row>
										<v-row
											cols="12"
											md="6">
											<v-select
												:disabled="loading"
												required
												:rules="[requiredRule]"
												item-text="label"
												:items="academic_results_list"
												label="Akademik göstəricilər"
												v-model="academic_result"
											/>
										</v-row>
										<v-row
											cols="12"
											md="4"
										>
											<v-text-field
												:disabled="loading"
												number
												v-model="financial_status"
												:rules="[requiredRule]"
												label="Maddi vəziyyətiniz"
												required
											/>
										</v-row>
										<v-row
											cols="12"
											md="6">
											<v-select
												:disabled="loading"
												required
												:rules="[requiredRule]"
												item-text="label"
												:items="live_place_list"
												label="Harada yaşayırsınız"
												v-model="live_place"
											/>
										</v-row>
									</v-col>
								</v-row>
								<v-row class="d-flex flex-row justify-center">
									<v-btn type="submit"
										   form="data-form"
										   elevation="12"
										   width="50%"
										   min-height="60"
										   :disabled="loading"
										   class="my-5 align-self-start"
										   color="primary"
										   @click="handleSubmit"
										   large
										   outlined>
										Növbəti
										<v-icon>mdi-chevron-right</v-icon>
									</v-btn>
