							</v-col>
						</v-layout>
					</v-container>
				</v-form>
			</v-col>
		</v-layout>
	</v-container>
</template>

<script>
	import {mapState} from "vuex";
	import {dataStore} from "../firebase";
	import {
		academic_results_list,
		education_list,
		genders,
		live_place_list,
		martial_status_list,
		residance_condition_list,
		residance_place_list,
		social_status_list
	} from "./Statics";
	import {form_not_valid_message} from "@/components/messages";

	export default {
		// lifecycle hooks
		data() {
			return ({
				genders,
				social_status_list,
				academic_results_list,
				education_list,
				live_place_list,
				martial_status_list,
				residance_condition_list,
				residance_place_list,
				valid: false,
				id: null,
				firstname: "",
				lastname: "",
				email: "",
				age: "",
				gender: "",
				social_status: "",
				academic_result: "",
				education: "",
				live_place: "",
				martial_status: "",
				residance_condition: "",
				family_members: "",
				financial_status: "",
				residance_place: "",
				loading: false
			})
		},
		mounted() {
			this.$store.dispatch('setActiveStepper', this.$route.meta.index);
			this.toast = this.$swal.mixin({
				toast: true,
				position: 'top-end',
				type: 'error',
				showConfirmButton: false,
				timer: 5000,
				showCloseButton: true,
				padding: '10px',
				onOpen: (toast) => {
					toast.addEventListener('mouseenter', this.$swal.stopTimer);
					toast.addEventListener('mouseleave', this.$swal.resumeTimer)
				}
			});
		},
		computed: {
			...mapState(["userId", "user"]),
			fullname() {
				return this.firstname + " " + this.lastname;
			}
		},
		methods: {
			ageRule(v) {
				return (v > 0 && v < 100) || "Age is not real";
			},
			requiredRule(v) {
				return !!v || 'Field is required';
			},
			numberRule(v) {
				return v > 0 || "Field value must be greater than zero";
			},
			handleSubmit() {
				this.loading = true;
				const isValid = this && this.$refs.dataForm.validate();
				if (isValid && this.userId) {
					const data = {
						personalData: {
							id: this.userId,
							firstname: this.firstname,
							lastname: this.lastname,
							age: this.age,
							email: this.email,
							gender: this.gender,
							social_status: this.social_status,
							academic_result: this.academic_result,
							education: this.education,
							live_place: this.live_place,
							martial_status: this.martial_status,
							residance_condition: this.residance_condition,
							family_members: this.family_members,
							financial_status: this.financial_status,
							residance_place: this.residance_place
						},
						personaldata: true,
					};
					dataStore.collection("results")
						.doc(this.userId)
						.set(data)
						.then(() => {
							this.$cookies.set("userId", this.userId);
							data.personaldata = "CONFIRMED";
							this.$store.dispatch("setUser", data);
							this.$router.push({name: "schwartz1"});
						})
						.finally(() => this.loading = false);
				} else {
					this.toast.fire({
						type: 'error',
						title: form_not_valid_message
					});
					this.loading = false
				}
			},
			restoreData({personalData}) {
				this.$store.dispatch("setUserId", personalData.id);
				this.firstname = personalData.firstname;
				this.lastname = personalData.lastname;
				this.age = personalData.age;
				this.email = personalData.email;
				this.gender = personalData.gender;
				this.social_status = personalData.social_status;
				this.academic_result = personalData.academic_result;
				this.education = personalData.education;
				this.live_place = personalData.live_place;
				this.martial_status = personalData.martial_status;
				this.residance_condition = personalData.residance_condition;
				this.family_members = personalData.family_members;
				this.financial_status = personalData.financial_status;
				this.residance_place = personalData.residance_place;
			}
		},
		watch: {
			user() {
				const __user = this.user;
				if (__user) {
					console.log("Found your user data restoring data in seconds ..");
					this.restoreData(__user);
				}
			}
		}
	};
</script>

<style>
	.Form {
		#data-form {
			width: 900px;
		}
	}
</style>
