<template>
	<v-container>
		<v-layout>
			<v-sheet elevation="1" max-width="100%">
				<v-col cols="12" class="pa-0">
					<v-banner class="banner" min-width="100%" elevation="10" dark>
						<p class="banner-text display-1 font-weight-bold">
							A.BASS VƏ A.DARKI. AQRESSİYANIN GÖSTƏRİCİLƏRİ VƏ
							FORMALARININ DİAQNOSTİKASININ METODİKASI
						</p>
					</v-banner>
					<v-banner class="banner" min-width="100%" elevation="10" dark>
						<p class="banner-text heading font-weight-bold">
							A.K.Osnitski tərəfindən uyğunlaşdırılmışdır.
						</p>
					</v-banner>
					<v-sheet style="background-color: #E8470C" elevation="8" class="pa-6 text-justify">
						<p class="headline">
							TƏLİMAT: Verilmiş mülahizələri oxuyaraq onların sizin davranış stilinizə,
							sizin həyat tərzinizə nə qədər uyğun gəldiyini təxminən müəyyən edin və dörd mümkün cavabdan
							birini seçin.
						</p>

					</v-sheet>
					<v-divider class="my-6"/>
					<v-banner dark
							  class="banner d-flex justify-center"
							  min-width="100%"
							  elevation="10">
						<template>
							<p class="mb-0 paragraph-fill headline font-weight-bold text-center">
								Daha az əmin olduğunuz sualları cavablarkən yəqin prefiksindən istifadə edə bilərsiniz.
							</p>
						</template>
					</v-banner>
					<v-divider class="my-1"/>
					<v-sheet light elevation="12"
							 class="question-sheet d-flex flex-row flex-wrap justify-space-around align-center">
						<QuestionType3
							v-for="(q,i) in agressionQuiz"
							:key="q.id"
							:id="q.id"
							@handleResult="handleResult"
							:title="q.label"
							:err="errors[i]"
						/>
					</v-sheet>
					<v-row justify="center" align-content="center" no-gutters>
						<v-btn elevation="12"
							   width="50%"
							   min-height="60"
							   class="my-5 align-self-start"
							   type="submit"
							   color="primary"
							   @click="handleSubmit"
							   large
							   outlined>
							Növbəti
							<v-icon>mdi-chevron-right</v-icon>
						</v-btn>
					</v-row>
				</v-col>
			</v-sheet>
		</v-layout>
	</v-container>
</template>

<script>
	import {dataStore} from "../firebase";
	import QuestionType3 from "./QuestionType3";
	import {agressionQuiz} from "./Statics";
	import {mapState} from "vuex";
	import {success_done_message, user_does_not_exist_title, user_does_not_exist_message} from "./messages";

	export default {
		name: "Agression",
		mounted() {
			if (this.user === null) {
				alert('No user');
				this.$router.replace('/')
			}
			this.$store.dispatch("setActiveStepper", this.$route.meta.index);
		},
		components: {
			QuestionType3
		},
		created() {
			this.toast = this.$swal.mixin({
				toast: true,
				position: 'top-end',
				type: 'error',
				showConfirmButton: false,
				timer: 3000,
				showCloseButton: true,
				padding: '10px',
				onOpen: (toast) => {
					toast.addEventListener('mouseenter', this.$swal.stopTimer);
					toast.addEventListener('mouseleave', this.$swal.resumeTimer)
				}
			});
		},
		data: () => ({
			agressionQuiz: agressionQuiz,
			errors: [],
		}),
		methods: {
			isValid() {
				let valid = true;
				this.errors = this.agressionQuiz.map(q => {
					if (q.opinion !== 0 && !q.opinion) {
						valid = false;
						return true;
					} else return false
				});
				return valid;
			},
			handleResult(id, opinion) {
				this.agressionQuiz = this.agressionQuiz.map(q => {
					if (q.id === id) {
						q.opinion = opinion;
					}
					return q;
				});
			},
			handleSubmit() {
				if (this.isValid()) {
					if (this.user && this.user.personalData.id) {
						this.user.agressionQuizResults = this.agressionQuiz;
						this.user.quiz5 = true;
						dataStore.collection("results")
							.doc(this.user.personalData.id)
							.set(this.user)
							.then(() => {
								this.$store.dispatch('setUser', this.user);
								this.toast.fire({
									type: 'success',
									title: success_done_message
								});
								this.$router.push({name: "done"});
							});
					} else {
						this.$swal({
							title: user_does_not_exist_title,
							text: user_does_not_exist_message,
							type: "error",
							showCancelButton: true,
							showConfirmButton: false,
							cancelButtonText: "OK"
						});
					}
				} else {
					this.toast.fire({
						type: 'error',
						title: form_not_valid_message
					})
				}
			}
		},
		computed: {
			...mapState(["user"])
		},
	}
</script>

<style scoped>
	.question-sheet {
		height: 800px;
		padding: 2rem;
		overflow: scroll;
	}

	.banner {
		color: #ffffff;
		display: flex;
		text-align: center;

	}

	.banner-text {
		margin: 0;
	}

	.list {
		text-decoration: none;
		list-style: none;
		display: flex;
		justify-content: space-evenly;
		padding: 0;
	}

	.list li {
		/*border: 1px solid #fff;*/
	}

	.v-banner__text {
		width: 100%;
	}

	.paragraph-fill {
		width: 100%;
	}
</style>
