<template>
	<v-row class="table-row">
		<v-col cols="1">
			<v-row align="center" justify="center" style="height: 100%">
				<p class="pl-1">{{this.id+1}}.</p>
			</v-row>
		</v-col>
		<v-col cols="4">
			<p class="pl-4" :style="{color: this.error?'#ff5252':'#ffffff'}">{{title}}</p>
		</v-col>
		<v-col cols="7">
			<v-row no-gutters>
				<v-radio-group v-model="opinion"
							   row
							   style="width: 100%"
							   v-on:change="handleChange"
							   mandatory
							   :error="error">
					<v-col class="table-col text-center"
						   v-for="k in this.types" :key="k.id">
						<v-radio :value="k.id" name="check" class="text-center"/>
					</v-col>
				</v-radio-group>
			</v-row>
		</v-col>
	</v-row>
</template>

<script>
	import {schwartzQuizOpinionTypes} from "./Statics";

	export default {
		name: "QuestionType2",
		props: {
			title: {
				type: String,
				default() {
					return ""
				}
			},
			id: {
				type: Number,
				default() {
					return null
				}
			},
			types: {
				type: Array,
				default() {
					return [];
				}
			},
			error: {
				type: Boolean,
				default: () => false,
				required: true
			},
			test: {
				type: String,
				required: true
			}
		},
		data() {
			return {
				schwartzQuizOpinionTypes,
				opinion: ""
			}
		},
		methods: {
			handleChange(event) {
				console.log(this.opinion);
				this.$emit("handleResult", this.id, this.opinion);
				this.$emit("handleError", this.id, false);
			}
		}
	}
</script>

<style lang="scss">
	.v-input__control {
		width: 100% !important;
	}

	.v-input__label {
		width: 100% !important;
	}

	.table-row {
		width: 100% !important;
	}

	.table-col {
		padding: 0;
		margin: 0;
		width: 20% !important;
	}

	.v-radio {
		display: flex;
		justify-content: center;
		padding: 0;
		margin: 0;
	}

</style>
