<template>
	<v-app>
		<v-app-bar app color="primary" dark>
			<router-link to="/">
				<div class="d-flex align-center">
					<v-img
						alt="Vuetify Logo"
						class="shrink mr-2"

						contain
						src="https://cdn.vuetifyjs.com/images/logos/vuetify-logo-dark.png"
						transition="scale-transition"
						width="40"
					/>
					<p class="ml-2 mb-0 text-uppercase" style="color: white">{{title}}</p>

				</div>
			</router-link>
			<v-spacer/>
			<v-row align-content="space-between">
				<v-stepper :value="activeStepper" class="stepper" dark>
					<v-stepper-header>
						<v-stepper-step v-for="(r, i) in routes" :step="i+1" :key="i">
							{{r.meta.title}}
						</v-stepper-step>
					</v-stepper-header>
				</v-stepper>
			</v-row>
		</v-app-bar>
		<v-content>
			<router-view/>
		</v-content>
	</v-app>
</template>

<script>
	import {dataStore} from "./firebase";
	import {mapState} from "vuex";
	import {methods} from "./router";

	export default {
		name: 'App',
		data() {
			return {
				routes: methods,
				step: this.$route.meta.index,
				isTestMode: false, /* process.env.NODE_ENV === 'development',*/
				title: this.$route.meta.title
			}
		},
		mounted() {
			this.toast = this.$swal.mixin({
				toast: true,
				position: 'top-end',
				type: 'error',
				showConfirmButton: false,
				timer: 5000,
				showCloseButton: true,
				padding: '10px',
				onOpen: (toast) => {
					toast.addEventListener('mouseenter', this.$swal.stopTimer);
					toast.addEventListener('mouseleave', this.$swal.resumeTimer)
				}
			});
		},
		created() {
			console.log('env -> ', process.env);
			try {
				if (this.isTestMode) {
					const __id = '82c31ae7-1d7e-4dcc-814d-596b28161027';
					dataStore
						.collection('results')
						.doc(__id)
						.get()
						.then((doc) => {
							if (doc.exists) {
								console.log("Found data 😎 ", doc.data());
								this.$store.dispatch("setUser", doc.data());
							} else {
								console.log("There is no user data creating id for new user ..");
								const userId = this.uuidv4();
								console.log("new user id -> ", userId);
								this.$store.dispatch("setUserId", userId);
							}
						}).catch(ex => {
						console.log("There is no user data creating id for new user ..");
						const userId = this.uuidv4();
						console.log("new user id -> ", userId);
						this.$store.dispatch("setUserId", userId);
					});
				} else if (this.$cookies.isKey("userId")) {
					const id = this.$cookies.get("userId");
					console.log("***there is a userId key in cookies -> ", id);
					dataStore
						.collection('results')
						.doc(id)
						.get()
						.then((doc) => {
							if (doc.exists) {
								console.log("Found data 😎 ", doc.data());
								this.$store.dispatch("setUser", doc.data());
							} else {
								this.$cookies.remove('userId');
								this.toast.fire({
									type: 'error',
									title: 'Internal error'
								}).finally(() => this.$router.go());
							}
						}).catch(ex => {
						console.log(ex.message);
					});
				} else {
					console.log("There is no user data creating id for new user ..");
					const userId = this.uuidv4();
					this.$store.dispatch('setUser', null);
					console.log("new user id -> ", userId);
					this.$store.dispatch("setUserId", userId);
				}
			} catch (e) {
				if (e === 'nodata') {
					console.log("There is no user data creating id for new user ..");
					const userId = this.uuidv4();
					console.log("new user id -> ", userId);
					this.$store.dispatch("setUserId", userId);
				}
			}
		},
		components: {},
		watch: {
			activeStepper: function (value, oldValue) {
				this.title = this.$router.options.routes[value - 1].meta.title
			}
		},
		computed: {
			...mapState(['activeStepper'])
		},
		methods: {
			uuidv4() {
				return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
					const r = Math.random() * 16 | 0, v = c === 'x' ? r : (r & 0x3 | 0x8);
					return v.toString(16);
				});
			},
		}
	};
</script>


<style>

	.theme--dark.v-stepper .v-stepper__step {
		height: 95%;
	}

	.theme--dark.v-stepper .v-stepper__step--active {
		background-color: white;
	}

	.theme--dark.v-stepper .v-stepper__step--active .v-stepper__label {
		color: #151315;
	}

	.stepper {
		background-color: transparent !important;
		border: 0 !important;
		outline: 0 !important;
		box-shadow: none !important;

	}

	.v-stepper__header {
		flex-wrap: nowrap !important;
	}

	.link {
		color: #151315;
		border: 0;
		border-radius: 2px;
		text-decoration: none;
		padding: 8px;
		background-color: #fefefe;
		box-shadow: 0 0 5px 1px #3b3b3b;
	}

	.active-link {
		background-color: brown;
	}
</style>
